<!-- 我的工单头部 -->
<template>
  <van-row style="height:100%">
    <!-- 顶部蓝色框首页 -->
    <van-row type="flex"
             align="center"
             class="dt-title">
      <van-col span="2">
        <van-icon class="dt-back"
                  @click="back"
                  name="arrow-left" />
      </van-col>
      <van-col span="20">
        <p class="dt-home-shouye">我的工单</p>
      </van-col>
    </van-row>
    <van-tabs v-model="active">
      <van-tab :title="'待办('+totalCount1+')'">
        <orderList :sendForm="form1"
                   :formType="1">
        </orderList>
      </van-tab>
      <van-tab :title="'已办('+totalCount2+')'">
        <orderList :sendForm="form2"
                   :formType="2">
        </orderList>
      </van-tab>
      <van-tab :title="'我上报工单('+totalCount3+')'">
        <orderList :sendForm="form3"
                   :formType="3">
        </orderList>
      </van-tab>
    </van-tabs>
  </van-row>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';
import {
  GetPageContentByConditions, // 工单查询
} from "@/api/workOrder";
import orderList from "@/views/myOrder/orderList";
export default {
  //import引入的组件需要注入到对象中才能使用
  components: { orderList },
  data () {
    //这里存放数据
    return {
      sendForm: {},
      form1: { // 待办
        ChargeUserID: JSON.parse(localStorage.getItem("userInfo")).uid, // 接收人ID(自己)
        SecunityContent: '',// 工单内容
        SecunityStatus: 1, // 工单状态 Int 1.待处理，2.已办理,3.已指派
        OperationBeginDate: null, // 上报开始时间
        OperationEndDate: null, // 上报结束时间
        PageSize: 20, // 条数 Int 默认20
        PageIndex: 0, // 页码 Int 默认1
      },
      form2: { // 已办
        ChargeUserID: JSON.parse(localStorage.getItem("userInfo")).uid, // 接收人ID(自己)
        SecunityContent: '',// 工单内容
        SecunityStatus: 2, // 工单状态 Int 1.待处理，2.已办理,3.已指派
        OperationBeginDate: null, // 上报开始时间
        OperationEndDate: null, // 上报结束时间
        PageSize: 20, // 条数 Int 默认20
        PageIndex: 0, // 页码 Int 默认1
      },
      form3: { // 我上报工单
        OperatorUserId: JSON.parse(localStorage.getItem("userInfo")).uid, // 上报人人ID(自己)
        SecunityContent: '',// 工单内容
        // SecunityStatus: 3, // 工单状态 Int 1.待处理，2.已办理,3.已指派  //为了测试
        OperationBeginDate: null, // 上报开始时间
        OperationEndDate: null, // 上报结束时间
        PageSize: 20, // 条数 Int 默认20
        PageIndex: 0, // 页码 Int 默认1
      },
      totalCount1: 0, // 数据总数
      totalCount2: 0, // 数据总数
      totalCount3: 0, // 数据总数
      active: 0,//tab状态
    };
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {},
  //生命周期 - 创建完成（可以访问当前this实例）
  created () {

  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted () {
    this.initTotalCount()
    //将获取的导航栏下标赋值给页面的导航栏下标
    this.active = this.$store.state.active
  },
  beforeCreate () { }, //生命周期 - 创建之前
  beforeMount () { }, //生命周期 - 挂载之前
  beforeUpdate () { }, //生命周期 - 更新之前
  updated () { }, //生命周期 - 更新之后
  beforeDestroy () { }, //生命周期 - 销毁之前
  destroyed () { }, //生命周期 - 销毁完成
  activated () { }, //如果页面有keep-alive缓存功能，这个函数会触发
  //方法集合
  methods: {
    // 工单查询
    initTotalCount () {
      GetPageContentByConditions(this.form1).then(response => {
        this.totalCount1 = response.Data.TotalCount
      })
      GetPageContentByConditions(this.form2).then(response => {
        this.totalCount2 = response.Data.TotalCount
      })
      GetPageContentByConditions(this.form3).then(response => {
        this.totalCount3 = response.Data.TotalCount
      })
    },
    // 返回首页按钮
    back () {
      this.$store.commit("active", 0)
      history.back(-1);
    },

  },
}
</script>
<style lang='scss' scoped>
//@import url(); 引入公共css类
@import "../../../styles/dimens";
.dt-title {
  width: 100%;
  height: $headerHeight;
  padding-top: $statusBarHeight;
  background-color: #5870f8;
}
.dt-back {
  height: px2rem(14);
  width: px2rem(14);
  margin-left: px2rem(6);
  color: #fff;
  font-size: px2rem(18);
}
.dt-home-shouye {
  font-size: px2rem(17);
  font-family: "SourceHanSansCN-Medium";
  color: #ecedf2;
}
.van-tabs {
  height: calc(100% - #{$headerHeight} - #{$statusBarHeight});
}
/deep/.van-tabs__content {
  height: calc(100% - #{$thirdHeight});
}
/deep/.van-tab__pane,
.van-tab__pane-wrapper {
  height: 100%;
}
/deep/.van-tabs__line {
  background-color: #5870f8;
}
/deep/.van-tab--active > span {
  color: #5870f8;
}
</style>